*[data-alert="error"]{background-color: #f2dede !important; color: #a94442 !important; }
*[data-alert="warning"]{background-color: #fcf8e3 !important; color: #8a6d3b !important; }

//////////////////////////////////////////////////////////////
// VisualFeedback
//////////////////////////////////////////////////////////////
.VisualFeedback{
    position: fixed; z-index: 9999; padding: 0; margin: 0; max-width: 800px; max-height: 500px; overflow: auto;
}

.VisualFeedback[data-feedback-type="error"]{overflow: auto; top: 50%; left: 50%; transform: translate(-50%,-50%);}
.VisualFeedback[data-feedback-type="warning"]{top: auto; left: auto; right: 0; bottom: 0;}
.VisualFeedback[data-feedback-type="info"]{top: auto; left: auto; right: 0; bottom: 0;}

//////////////////////////////////////////////////////////////
// InputNumber
//////////////////////////////////////////////////////////////
.InputNumber{    text-align: right;}


//////////////////////////////////////////////////////////////
// Loading
//////////////////////////////////////////////////////////////
.Loading{
    position: absolute; top: 0; left: 0; z-index: 9999; display: none; pointer-events: none; width: 100%; height: 100%; /*background-color: rgba(0, 0, 0, 0.5);*/
    .Img{width: 128px; height: 128px; position: relative; top: 50%; left: 50%; margin-right: -50%; transform: translate(-50%, -50%); }
}


.groupContent {
    overflow-y: auto;
    height: 40vh;
    
.balon1, .balon2 {
	margin-top: 10px !important;
	margin-bottom: 10px !important;
}


.balon1 div {
	background: #42a5f5;
	color: #fff !important;
	border-radius: 20px 20px 3px 20px;
	display: block;
	max-width: 75%;
	padding: 7px 13px 7px 13px;
}


.balon2 div {
	background: #f1f1f1;
	color: #000 !important;
	border-radius: 20px 20px 20px 3px;
	display: block;
	max-width: 75%;
	padding: 7px 13px 7px 13px;
}
	
.balon1:after, .balon2:after {
	content: attr(data-is);
	position: absolute;
	left: 15px;
	bottom: -15px;
	display: block;
	font-size: .750rem;
	color: rgba(84, 110, 122,1.0);  
}

.balon1:after {
    left: unset;
    right: 15px;
}
}